<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">用户权限</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">组织机构</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="机构代码:" prop="dpid">
                <Input v-model="formValidate.dpid" placeholder="请输入机构代码" style="width:200px"></Input>
              </FormItem>
              <FormItem label="机构名称:" prop="dpname">
                <Input v-model="formValidate.dpname" placeholder="请输入机构名称" style="width:200px"></Input>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                  <Button type="primary" @click="handleSubmit('0')">查询</Button>
                  <Button type="primary" @click="handleSubmit('1')">精确查询</Button>
                </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td>{{item.dpid}}</td>
                  <td>{{item.dpname}}</td>
                  <td>{{item.parentDpname}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
          <div class="flexCenter pageBox">
            <Page :current.sync="pageNum" :total="rowCount" @on-change="pageChange" :page-size="pageSize" @on-page-size-change='pageSizefun' show-elevator show-sizer/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    let that = this;
    let data = {
      dpid: this.formValidate.dpid,
      dpname: this.formValidate.dpname,
      pageSize:this.pageSize,
      pageNum: this.pageNum,
      flag: this.searchFlag
    };
    data = JSON.stringify(data);
    this.axios
    .post("/system/unit/unitList", data, {
      headers: { "Content-Type": "application/json" }
    })
    .then(response => {
      if (response.code == "200") {
        that.rowCount = response.data.total;
        that.tableBody = response.data.records;
      }
    })
    .catch(error => {});
  },

  data() {
    return {
      tableHeader: [
        { name: "序" },
        { name: "机构代码" },
        { name: "机构名称" },
        { name: "上级机构" }
      ],
      pageNum: 1,
      searchFlag: 0,
      rowCount: 0,
      pageSize:10,
      tableBody: [
      ],
      formValidate: {
        dpid: "", //机构代码
        dpname: "" //机构名称
        // -----
      },
      ruleValidate: {
        dpid: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ],
        dpname: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    handleSubmit(flag) {
         this.searchFlag = flag * 1;
          // -----------
          let that = this;
          let data = {
            dpid: this.formValidate.dpid,
            dpname: this.formValidate.dpname,
            pageSize:this.pageSize,
            pageNum: 1,
            flag: this.searchFlag
          };
          data = JSON.stringify(data);
          this.axios
            .post("/system/unit/unitList", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                that.rowCount = response.data.total;
                that.tableBody = response.data.records;
              }
            })
            .catch(error => {});
    },
    pageSizefun(a){
      this.pageSize=a*1;
      if(this.pageNum == 1){
          this.pageChange(1);
      }
    },
     pageChange(pageNo) {
      this.pageNum = pageNo * 1;
      let that = this;
      let data = {
        dpid: this.formValidate.dpid,
        dpname: this.formValidate.dpname,
        pageSize:this.pageSize,
        pageNum: this.pageNum,
        flag: this.searchFlag
      };
      data = JSON.stringify(data);
      this.axios
        .post("/system/unit/unitList", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
              that.rowCount = response.data.total;
              that.tableBody = response.data.records;           
          }
        })
        .catch(error => {});
    },
  }
};
</script>
<style scoped>
</style>



